	module.exports = {"data":{"markdownRemark":{"html":"<h2>Hexagon</h2>\n<p>A hexagon is a polygon with six sides. The sum of the internal angles totals 720°. In a regular hexagon, each individual angle measures 120°.</p>\n<img src=https://upload.wikimedia.org/wikipedia/commons/thumb/c/cb/Hexagon_1.svg/2000px-Hexagon_1.svg.png  alt=\"a regular hexagon\" height=\"150\" />\n<h3>Area</h3>\n<p>The formula for the area of a regular hexagon with side length <code>s</code> is:</p>\n<p><code>A = (3√3*s<sup>2</sup>)/ 2</code></p>\n<p>For a hexagon with a side length of 4, the area would be calculated as:</p>\n<p><code> (3√3*<b>4</b><sup>2</sup>)/ 2 ≈ 41.57</code></p>\n<h3>Examples</h3>\n<p>A natural example of a hexagonal grid are bees’ honeycombs.</p>\n<p><img src=https://upload.wikimedia.org/wikipedia/commons/3/3d/Honeycomb_15_03_2012.jpg alt=\"a honeycomb\" height=\"200\" /></br></p>\n<p>An unnatural example of a hexagonal grid is the New York County Supreme Court building on Foley Square, Manhattan:</p>\n<img src=http://www.secretsinplainsight.com/wp-content/uploads/NY-Supreme-Court.jpg alt=\"NYC Supreme Court Building\" height=\"200\" />","fields":{"slug":"/mathematics/hexagon/"},"frontmatter":{"title":"Hexagon"}}},"pathContext":{"slug":"/mathematics/hexagon/"}}

